import React, { useState, useEffect, useMemo } from "react";
import { config } from "../configs/endpoints.js";
import { Link } from "react-router-dom";
import axios from "axios";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import filterFactory, { dateFilter } from "react-bootstrap-table2-filter";

function Users() {

  function actionDisplay(cell, row, rowIndex) {
    return (
      <Link
        className="btn btn-primary"
        to={{
          pathname: `${process.env.PUBLIC_URL}/pages/userform`,
          search: `?id=${row.user_id}`,
        }}
      >
        Edit
      </Link>
    );
  }

  function statusDisplay(cell, row) {
    return row.status == 1 ? "Active" : "Inactive";
  }

  function indexDisplay(cell, row, rowIndex) {
    return rowIndex+1;
  }

  const columns = [
    {
      dataField: "{rowIndex}",
      text: "#",
      formatter: indexDisplay,
    },
    {
      dataField: "first_name",
      text: "First Name",
    },
    {
      dataField: "last_name",
      text: "Last Name",
    },
    {
      dataField: "mobile_number",
      text: "Mobile",
    },
    {
      dataField: "status",
      text: "Status",
      formatter: statusDisplay,
    },
    {
      dataField: "user_id",
      text: "Action",
      formatter: actionDisplay,
    }
  ];

  const [records, setRecords] = useState(null);
  useEffect(() => {
    axios.get(getEndpoint()).then((response) => {
      setRecords(response.data);
    });
  }, []);

  function getEndpoint() {
    return `${config.endpoints.basePath}${config.endpoints.users.list}`;
  }

  return (
    <div>
      <div className="row">
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb">
            <li className="breadcrumb-item">
              <a href="!#" onClick={(event) => event.preventDefault()}>
                App
              </a>
            </li>
            <li className="breadcrumb-item active" aria-current="page">
              Users
            </li>
          </ol>
        </nav>
      </div>
      <div className="page-header">
        <h3 className="page-title"> Users </h3>
        <Link
          className="btn btn-primary"
          to={{
            pathname: `${process.env.PUBLIC_URL}/pages/userform`,
          }}
        >
          Add User
        </Link>
      </div>

      <div className="row">
        <div className="col-lg-12 grid-margin stretch-card">
          <div className="card">
            <div className="card-body">
              <div className="table-responsive">
                {records && (
                  <BootstrapTable
                    keyField="user_id"
                    data={records?.users}
                    columns={columns}
                    pagination={paginationFactory()}
                  />
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Users;
